import namespace from 'mapea-util/decorator';
import visorControl from './visorcontrol.js';
import catastroControl from './catastrocontrol.js';
import css from 'assets/css/plg_cartografia_historica.css';
import "@fortawesome/fontawesome-free/css/all.css";

@namespace("M.plugin")
class PLG_Cartografia_Historica extends M.Plugin {

  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @api stable
   */
  constructor(params) {

    super();
    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    /**
     * add your variables
     *
     */
    this.html_ = null;
    this.layers_ = params.layers;

    this.posx_ = null;
    this.posy_ = null;
    this.sizex_ = null;
    this.sizey_ = null;
  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    var visor = new M.control.visorControl(this.layers_, this);
    var catastro = new M.control.catastroControl(this);
    this.controls_.push(visor);
    this.controls_.push(catastro);
    this.map_ = map;
    this.html_ = null;
    //panel para agregar control - no obligatorio
    this.panel_ = new M.ui.Panel("panelvisor", {
      collapsible: true,
      position: M.ui.position.TR,
      className: "m-visor",
      collapsedButtonClass: "g-cartografia-flecha-izquierda"
    });
    this.panel_.addControls(this.controls_);
    map.addPanels(this.panel_);
    
    map.on(M.evt.COMPLETED, function(){
      setTimeout(function(){
        document.querySelector(".m-visor .m-panel-btn").addEventListener("click", function(){
          if(document.querySelector(".m-visor").classList.contains("opened") && !visor.activated && !catastro.activated){
            visor.activate();
          }
        });
      }, 100);
    });
  }

  getPosX(){
    return this.posx_;
  }
  getPosY(){
    return this.posy_;
  }
  setPosX(posx){
    this.posx_ = posx;
  }
  setPosY(posy){
    this.posy_ = posy; 
  }

  getSizeX(){
    return this.sizex_;
  }
  getSizeY(){
    return this.sizey_;
  }
  setSizeX(sizex){
    this.sizex_ = sizex;
  }
  setSizeY(sizey){
    this.sizey_ = sizey; 
  }
}



